/***************************
CHAT LEFT
****************************/

.chat__left {
    width: 20%;
    height: 93vh;
}


/********* chat left heard ***********/

.chat__left-heard {
    padding: 0 2px 0 2px;
    justify-content: space-between;
    background-color: $blanco-2;
    display: flex;
    color: $negro-0;
    height: 5%;
}

.chat__left-heard-left {
    margin-top: 5px;
}

.chat__left-heard-right {
    display: flex;
}

.chat__left-heard-right-icon {
    display: block;
    text-align: center;
    margin: 4px 3px 3px 3px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    cursor: pointer;
    background-color: $blanco-2;
    color: $iconos;
    &:hover {
        background-color: $morado;
        color: $blanco-2;
        box-shadow: 0 0 10px rgb(0, 0, 0);
        i {
            color: $blanco-2;
        }
    }
}

.chat__left-heard-right-icon i {
    margin-top: 8px;
    color: $morado;
}

.contenedor__iniciar-chat {
    position: absolute;
    margin-top: 35px;
    background-color: red;
    height: 80%;
    width: 18%;
    z-index: 1;
    border-radius: 5px;
    background-color: $blanco-1;
    border: solid 0.1px black;
}

.contenedor__iniciar-chat-body-item {
    display: flex;
}

.contenedor__iniciar-chat-body-item {
    padding-left: 9px;
    padding-top: 5px;
    padding-bottom: 5px;
    &:hover {
        background-color: rgb(77, 74, 74);
        cursor: pointer;
    }
}

.contenedor__iniciar-chat-body-item-left img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.contenedor__iniciar-chat-body-item-right {
    margin-left: 5px;
}

.contenedor__iniciar-chat-header {
    text-align: center;
    font-size: 20px;
    padding: 7px;
    font-weight: bold;
    background-color: $blanco-1;
}

.contenedor__option-chat {
    position: absolute;
    margin-top: 35px;
    margin-left: 35px;
    background-color: red;
    width: 12%;
    z-index: 1;
    border-radius: 5px;
    background-color: $blanco-1;
    border: solid 0.1px $negro-0;
}

.contenedor__option-chat-btn {
    text-align: center;
    display: block;
    background-color: inherit;
    color: $negro-0;
    padding: 12px 6px;
    width: 100%;
    border: none;
    outline: none;
    cursor: pointer;
    transition: 0.3s;
    font-size: 17px;
    &:hover {
        background-color: rgb(77, 74, 74);
    }
}


/********* chat left search ***********/

.chat__left-search {
    background-color: $blanco-2;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 10%;
}

.chat__left-search-input {
    width: 90%;
    height: 20px;
    margin: 5px;
    font-size: 18px;
    color: $negro-0;
    background-color: $blanco-0;
    border: none;
    color: $negro-0;
    padding: 15px 20px;
    border-radius: 100px;
    margin-right: -20px;
    transition: all 0.2s;
    &:focus {
        outline: none;
        width: 95%;
    }
    &::-webkit-input-placeholder {
        font-size: 18px;
        color: $negro-0;
    }
}

.chat__left-search-button {
    position: relative;
    left: -10px;
    border: none;
    background-color: $blanco-0;
    cursor: pointer;
    transition: all 0.2s;
    &:focus {
        outline: none;
    }
    &:active {
        transform: translateY(2px);
    }
}

.search__icon {
    font-size: 20px;
    color: $morado;
}


/********* chat left list ***********/

.chat__left-list {
    color: $negro-0;
    background-color: $blanco-2;
    width: 100%;
    height: 85%;
    overflow-y: auto;
    &::-webkit-scrollbar {
        width: 7px;
        background-color: $fondo;
    }
    &::-webkit-scrollbar-thumb {
        background-color: $iconos;
        border-radius: 5px;
    }
}

.chat__left-list-item {
    background-color: $blanco-2;
    display: flex;
    color: $texto-principal;
    padding: 7px;
    cursor: pointer;
    &:hover {
        background-color: $blanco-0;
    }
}

.chat__left-list-item-photo img {
    border-radius: 50%;
    width: 50px;
    height: 50px;
}

.chat__left-list-item-info {
    margin-left: 5px;
    color: $negro-1;
}

.chat__left-list-item-info-last-message {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.chat__left-list-item-info-last-message-content {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: inline-block;
    max-width: 6rem;
}

.chat__left-list-item-info-last-message-content i {
    font-family: "Font Awesome\ 5 Free", sans-serif;
    font-weight: 501;
    color: $negro-1;
}

.chat__left-list-item-info-last-message-date {
    margin-left: 5px;
    color: $negro-1;
    font-size: 13px;
}


/***************************
CHAT RIGHT
****************************/

.chat__right {
    width: 80%;
    height: 93vh;
    background-color: #cec9b8;
}

.chat__right-heard {
    height: 10%;
    padding: 7px;
    justify-content: space-between;
    background-color: $blanco-2;
    display: flex;
    color: $negro-0;
    i {
        color: $morado;
    }
}

.chat__right-heard-left {
    margin-top: 5px;
    display: flex;
}

.chat__right-heard-right {
    display: flex;
}

.chat__right-heard-right-icon {
    margin: auto;
    font-size: 25px;
    color: $negro-0;
    cursor: pointer;
    margin-left: 5px;
}

.chat__right-heard-left-photo img {
    border-radius: 50%;
    width: 45px;
    height: 45px;
}

.chat__right-heard-left-info {
    margin-left: 9px;
}

.chat__right-list-item-info-nombre {
    font-size: 18px;
}

.chat__right-list-item-info-nombre-nopersonal {
    font-size: 20px;
    margin-top: 10px;
}

.chat__right-list-item-info-last-conection {
    color: $negro-1;
    font-size: 14px;
}

.chat__right-heard-right-search {
    margin: auto;
}


/********* chat history ***********/

.chat__history {
    width: auto;
    height: 80%;
    background-color: #cec9b8;
    padding: 10px;
    overflow-y: auto;
    &::-webkit-scrollbar {
        width: 9px;
    }
    &::-webkit-scrollbar-thumb {
        background-color: $iconos;
        border-radius: 5px;
    }
}

.chat__message-left {
    display: flex;
    float: left;
    clear: both;
    position: relative;
    max-width: 50%;
}

.chat__message-photo img {
    border-radius: 50%;
    width: 30px;
    height: 30px;
}

.chat__message-info-left {
    margin-left: 7px;
    color: $negro-0;
    background-color: $blanco-2;
    border-radius: 10px;
    padding: 5px;
    border-top-left-radius: 0;
}

.chat__message-info-content {
    word-wrap: break-word;
}

.chat__message-info-content img {
    max-width: 100%;
    height: auto;
}

.chat__message-info-name {
    color: $negro-0;
    font-weight: bold;
}

.chat__message-info-right {
    position: relative;
    // margin-left: 10px;
    color: $negro-0;
    background-color: $blanco-2;
    border-radius: 10px;
    padding: 5px;
    border-bottom-right-radius: 0;
}

.chat__message-right {
    max-width: 50%;
    float: right;
    clear: both;
}

.chat__message-info-time {
    color: $negro-1;
    font-size: 14px;
    text-align: right;
}

.chat__message-info-time i {
    color: greenyellow;
}


/********** Send Message **********/

.chat__SendMessage {
    position: relative;
    height: 10%;
    width: auto;
    background-color: $blanco-2;
    color: $negro-1;
    display: flex;
    justify-content: space-between;
    i {
        color: $morado;
    }
}

.chat__SendMessage-left {
    display: flex;
    width: 8%;
}

.chat__SendMessage-left-icon {
    color: $negro-1;
    margin: auto;
    font-size: 20px;
}

.chat__SendMessage-left-icon label {
    display: inline-block;
    color: $negro-1;
    // padding: 0.5rem;
    font-family: sans-serif;
    border-radius: 0.3rem;
    cursor: pointer;
    // margin-top: 1rem;
}

.chat__SendMessage-right {
    width: 92%;
    margin: auto;
    margin-right: 15px;
    height: 50%;
}

.chat__SendMessage-right-input {
    height: 100%;
    border-radius: 10px;
    color: $negro-1;
    outline: none;
    border: none;
    background-color: $blanco-0;
    margin-right: 15px;
    width: 100%;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    resize: none;
    margin: auto;
    &::-webkit-scrollbar {
        width: 9px;
    }
    &::-webkit-scrollbar-thumb {
        background-color: $iconos;
        border-radius: 5px;
    }
}

