/******** chat info ***********/

.chat__info {
    width: 100%;
    height: 93vh;
    background-color: $blanco-0;
    color: $negro-0;
    border: solid 1px #000000;
    overflow-y: auto;
    &::-webkit-scrollbar {
        width: 9px;
    }
    &::-webkit-scrollbar-thumb {
        background-color: $negro-0;
        border-radius: 5px;
    }
}

.chat__info-heard {
    padding: 20px 40px 0px 40px;
    width: 100%;
    background-color: $blanco-2;
    text-align: center;
}

.chat__info-heard-photo {
    position: relative;
}

.chat__info-heard-photo img {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    text-align: center;
}

.chat__info-heard-info {
    width: 100%;
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    margin-top: 5px;
}

.chat__info-heard-info i {
    &:hover {
        cursor: pointer;
    }
}

.chat__info-heard-info span {
    color: $tecto-opaco;
    font-size: 15px;
}

.chat__info-body {
    height: 60%;
}

.chat__info-body-btn {
    height: 10%;
    cursor: pointer;
    text-align: center;
    align-items: center;
    background: #1f1c1c;
    border: solid 0.1px black;
}

.chat__info-body-medias {
    // padding: 0px 5px;
    width: 100%;
    height: 90%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 0.5rem;
    row-gap: 0.5rem;
    overflow-y: auto;
    &::-webkit-scrollbar {
        width: 9px;
    }
    &::-webkit-scrollbar-thumb {
        background-color: $iconos;
        border-radius: 5px;
    }
}

.chat__info-body-medias-item img {
    max-width: 160px;
    height: auto;
}

.chat__info-body-medias-item video {
    max-width: 160px;
    height: auto;
}


/*********Modal create grupo*********/

.creategrupo__modal-body {}

.creategrupo__modal-body-top {
    display: flex;
    justify-content: center;
}

.creategrupo__modal-body-picture {
    color: $iconos;
    background-color: red;
    font-size: 2em;
    border-radius: 50%;
    border: solid white;
    color: white;
    line-height: 2em;
    width: 2em;
    height: 2em;
    text-align: center;
    display: inline-block;
    transition: 0.5s;
}

.creategrupo__modal-body-picture img {
    color: $iconos;
    background-color: red;
    border-radius: 50%;
    border: solid white;
    color: white;
    margin-bottom: 20px;
    margin-left: -3px;
    width: 2em;
    height: 2em;
    text-align: center;
    display: inline-block;
}

.creategrupo__modal-body-name {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 10px;
}

.creategrupo__modal-body-name input {
    border: none;
    border-bottom: 1px solid #37e262;
    padding: 5px 10px;
    outline: none;
    color: $texto-principal;
    background-color: $fondo-panel;
}

.creategrupo__modal-footer {
    float: right;
    clear: both;
}

.createcanal__modal-header {
    color: $texto-principal;
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 5px;
}

.createcanal__modal-body-users {
    color: $texto-secundario;
    max-height: 450px;
    overflow-y: auto;
    width: 250px;
    min-height: 400px;
    padding-right: 20px;
    &::-webkit-scrollbar {
        width: 4px;
    }
    &::-webkit-scrollbar-thumb {
        background-color: $iconos;
        border-radius: 5px;
    }
}

.createcanal__modal-body-search input {
    border: none;
    border-bottom: 1px solid #37e262;
    width: 100%;
    outline: none;
    color: $texto-principal;
    background-color: $fondo-panel;
    margin-bottom: 3px;
}

.contenedor__iniciar-chat-body-search {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 3px;
    padding: 10px;
}

.contenedor__iniciar-chat-body-search input {
    border: none;
    border-bottom: 1px solid #37e262;
    width: 90%;
    outline: none;
    color: $negro-0;
    background-color: $blanco-2;
}

.chat__userlected {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    max-height: 100px;
    overflow-y: auto;
    &::-webkit-scrollbar {
        width: 5px;
        background-color: $fondo;
    }
    &::-webkit-scrollbar-thumb {
        background-color: $iconos;
        border-radius: 5px;
    }
}

.chat__userlected-item {
    color: $texto-secundario;
    display: flex;
    border-radius: 30px;
    background-color: #4d4783;
}

.chat__userlected-item div {
    padding: 5px;
    width: 100%;
    display: inline-block;
    margin-left: auto;
    margin-right: auto;
}

.chat__userlected img {
    width: 30px;
    height: 30px;
    border-radius: 50%;
}


/***no chat active *****/

.chat__right-noactive {
    text-align: center;
    color: $negro-0;
    background-color: $blanco-2;
    font-size: 25px;
    font-weight: bold;
    margin-top: 30%;
}

.chat__right p {
    text-align: center;
    color: $negro-0;
    background-color: $blanco-2;
    font-size: 20px;
    font-weight: bold;
}

.loader__message-file {
    margin-left: 5px;
    border: 5px solid #f3f3f3;
    /* Light grey */
    border-top: 5px solid #3498db;
    /* Blue */
    border-radius: 50%;
    width: 25px;
    height: 25px;
    animation: spin 2s linear infinite;
    @keyframes spin {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
}

.chat__info-body-users {
    margin-top: 5px;
    background-color: $blanco-2;
}

.chat__info-body-users-list {
    padding-top: 5px;
}

.chat__info-body-users-list-item {
    display: flex;
    margin: 5px;
    &:hover {
        background-color: $fondo-contenido;
        cursor: pointer;
    }
}

.chat__info-body-users-list-item-photo {
    position: relative;
}

.chat__info-body-users-list-item-photo-online {
    position: absolute;
    background-color: #36944d;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    margin-left: -15px;
    margin-top: 35px;
    border: 1px solid black;
}

.chat__info-body-users-list-item-photo img {
    border-radius: 50%;
    width: 50px;
    height: 50px;
}

.chat__info-body-users-list-item-info {
    display: flex;
    position: relative;
    margin-left: 5px;
}

.chat__info-body-users-title {
    padding-top: 5px;
    margin-left: 10px;
    font-size: 17px;
    font-weight: bold;
    display: flex;
    justify-content: space-between;
}

.chat__info-body-users-title-right {
    margin-right: 5px;
}

.chat__info-body-users-title-right i {
    margin-right: 12px;
    &:hover {
        cursor: pointer;
    }
}

.chat__info-body-users-list-item-info span {
    position: absolute;
    color: #38a353;
    border: solid 1px #37e262;
    padding: 5px;
    margin-left: 170px;
    border-radius: 5px;
    font-size: 13px;
}

.btn__show-more {
    color: #38a353;
    margin-left: 8px;
    &:hover {
        cursor: pointer;
    }
}

.chat__info-body-multimedias {
    margin-top: 5px;
    background-color: $blanco-2;
}

.chat__info-body-multimedias-btn {
    font-size: 16px;
    padding: 5px;
    min-width: 330px;
    font-weight: bold;
    &:hover {
        background-color: $fondo-contenido;
        cursor: pointer;
    }
}

.chat__info-body-multimedias-btn i {
    margin-left: 40px;
}

.chat__info-body-multimedias-btn span {
    margin-left: 40px;
}

.chat__info-heard-info-description {
    text-align: start;
    display: flex;
    padding-bottom: 5px;
    margin-top: 5px;
}

.chat__info-heard-info-description-icon {
    font-size: 20px;
    margin-right: 20px;
}

.chat__info-heard-info-description-data {
    max-width: 320px;
    position: relative;
}

.chat__info-heard-photo-update {
    color: $texto-principal;
    background-color: $fondo-contenido;
    font-size: 70px;
    margin-top: -200px;
    margin-left: 50px;
    position: absolute;
    text-align: center;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    opacity: 0;
    &:hover {
        opacity: 0.6;
        cursor: pointer;
    }
}

.chat__info-heard-photo-update i {
    text-align: center;
    margin-top: 35%;
}

.updatechat__modal {
    margin: auto;
    min-width: 300px;
    padding: 0px;
    width: 100%;
    height: 100%;
    text-align: center;
    background-color: $fondo;
}

.updatechat__modal-picture {
    padding: 40px 0px;
    width: 100%;
    height: 100%;
    background-color: $fondo-panel;
    margin-bottom: 5px;
}

.updatechat__modal-picture img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
}

.updatechat__modal-data-update {
    padding: 20px 30px;
    width: 100%;
    background-color: $fondo-panel;
}

.updatechat__modal-form {
    position: relative;
    height: 48px;
    margin-bottom: 1.5rem;
}

.updatechat__modal-form-input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 0.1rem;
    border: none;
    border-bottom: 1px solid $texto-secundario;
    outline: none;
    padding: 1rem;
    background: none;
    font-size: $texto-secundario;
    z-index: 1;
    color: $texto-secundario;
}

.for__modal-form-input {
    position: absolute;
    left: 1rem;
    top: 1rem;
    padding: 0 0.25rem;
    color: $tecto-opaco;
    transition: 0.3s;
}

.updatechat__modal-form-input:focus+.for__modal-form-input {
    top: -0.5rem;
    left: 0.8rem;
    z-index: 10;
    font-size: 10px;
    font-weight: 500;
}


/*Input focus sticky top label*/

.updatechat__modal-form-input:not(:placeholder-shown).updatechat__modal-form-input:not(:focus)+.for__modal-form-input {
    top: -0.5rem;
    left: 0.8rem;
    z-index: 10;
    font-size: var(--small-font-size);
    font-weight: 500;
}

.updatechat__modal-info-update {
    color: $texto-secundario;
    padding: 5px 0;
    width: 100%;
    text-align: start;
    background-color: $fondo-panel;
}

.updatechat__modal-info-update-type {
    display: flex;
    justify-content: space-between;
    padding: 0 40px;
    color: $texto-secundario;
}

.updatechat__modal-info-update-members {
    margin-top: 6px;
    text-align: start;
    background-color: $fondo-panel;
    color: $texto-secundario;
}

.updatechat__modal-info-update-members-admins {
    padding-left: 40px;
    padding: 7px;
    &:hover {
        cursor: pointer;
        background-color: $fondo-contenido;
    }
}

.updatechat__modal-info-update-members-admins i {
    padding-left: 40px;
}

.updatechat__modal-info-update-members-admins span {
    margin-left: 40px;
}

.updatechat__modal-btn {
    text-align: right;
    margin-top: 5px;
    background-color: $fondo-panel;
    padding: 10px;
}

